/* Apply a basic style */
body {
    font-family: Arial, sans-serif;
    /* background-color: #f4f4f4; */
    margin: 0;
    padding: 0;
    text-align: center;
}

/* Center the App */
#root {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
}

/* Example button styling */
button {
    padding: 10px 20px;
    font-size: 16px;
    background-color: #007bff;
    color: white;
    border: none;
    cursor: pointer;
    border-radius: 5px;
}

button:hover {
    background-color: #0056b3;
}